let _self;
export default {
	data() {
		return {
			isok: true,
			address: ''
		};
	},
	onLoad() {
		_self = this;
		_self.getAddress();
	},
	methods: {
		getAddress() {
           let ad= uni.getStorageSync('address')
		   if(ad){
			   _self.address=ad
		   }
		},
		chooseAddress() {
			uni.chooseLocation({
				success: e => {
					console.log(e);
					_self.address = e.address;
					uni.setStorageSync("address",e.address)
					
				}
			});
		}
	}
};
